@mixin clearfix() {
  &::after {
    content: '';
    clear: both;
    display: table;
  }
}

@mixin placeholder-color($placeholder-color) {
  ::-webkit-input-placeholder {
    color: $placeholder-color;
  }

  :-moz-placeholder {
    color: $placeholder-color;
  }

  ::-moz-placeholder {
    color: $placeholder-color;
  }

  :-ms-input-placeholder {
    color: $placeholder-color;
  }
}

// Gradient color
@mixin gradient-color($value, $deg) {
  @if $value==a {
    background: linear-gradient(
      $deg#{deg},
      #f71595 0%,
      #ed560e 62%,
      #ff9600 100%
    );
    background-clip: text;
    text-fill-color: transparent;
  } @else if $value==b {
    background: linear-gradient(
      $deg#{deg},
      rgb(251, 110, 57) 12%,
      rgb(252, 85, 93) 50%,
      rgb(253, 59, 128) 91%
    );
    background-clip: text;
    text-fill-color: transparent;
  } @else if $value==c {
    background: linear-gradient(
      $deg#{deg},
      rgb(40, 172, 225) 12%,
      rgb(81, 204, 231) 50%,
      rgb(122, 235, 236) 91%
    );
    background-clip: text;
    text-fill-color: transparent;
  }
}

// Gradient Background
@mixin gradient-bg($value, $deg) {
  @if $value==a {
    background: linear-gradient($deg#{deg}, #ffffff 0%, transparent 95%);
  } @else if $value==b {
    background: linear-gradient(
      $deg#{deg},
      rgba(223, 32, 32, 1) 0%,
      rgba(0, 0, 0, 0.6) 36%,
      rgba(0, 0, 0, 0.3) 100%
    );
  } @else if $value==c {
    background: linear-gradient(
      $deg#{deg},
      rgb(40, 172, 225) 12%,
      rgb(81, 204, 231) 50%,
      rgb(122, 235, 236) 91%
    );
  }
}
